<template>
	<div id="location">
		<public-header title="选择收货地址" @onClickRight="util.jsUrl('/pages/member/addressPost', {channel: 'location'}, 'replace')">
			<div class="font-14" slot="right">新增地址</div>
		</public-header>
		<div class="container">
			<div class="search-bar">
				<form action="/">
					<van-search
						v-model="key"
						placeholder="请输入您的收货地址"
						shape="round"
						clearable
					>
						<div class="search-left" @click="util.jsUrl(city.url, {}, 'replace')" slot="left">
							<div class="icon icon-location"></div>
							<div class="city">{{city.title}}</div>
							<div class="icon icon-unfold" v-if="city.url"></div>
						</div>
					</van-search>
				</form>
			</div>
			<template v-if="!showSearch">
				<div class="van-hairline--bottom">
					<van-row class="now-address">
						<van-col span="16">
							<div class="address-text">{{locationInfo.address ? locationInfo.address : '获取定位失败'}}</div>
						</van-col>
						<van-col span="8">
							<div class="to-location" @click="onGetNowLocation">
								<div class="icon icon-locationfill font-16 margin-5-r"></div>
								<div>{{locationInfo.location_x ? '重新获取定位' : '获取定位'}}</div>
							</div>
						</van-col>
					</van-row>
				</div>
				<template v-if="addresses.length > 0">
					<div class="headline">
						<div class="icon icon-home"></div>
						<div>我的收货地址</div>
					</div>
					<div class="address-list">
						<div class="address-item van-hairline--bottom" @click="chooseLocation(item)" v-for="(item, index) in addresses" :key="index">
							<div class="address-text">
								<div class="label" :class="`label-${item.tag}`" v-if="item.tag >= 1 && item.tag <= 3">{{item.tag_cn}}</div>
								{{item.address}}
							</div>
							<div class="member">
								<div>{{item.realname}}</div>
								<div class="sex">{{item.sex}}</div>
								<div>{{item.mobile}}</div>
							</div>
						</div>
					</div>
				</template>
				<template v-else>
					<van-empty description="暂无收货地址" />
				</template>

			</template>
			<template v-else>
				<div class="search-list" v-if="searchAddress.length > 0">
					<div class="search-item" :class="{'van-hairline--top': index > 0}" @click="chooseLocation(item, true)" v-for="(item, index) in searchAddress" :key="index">
						<van-row>
							<van-col span="24">
								<div class="address-text ellipsis">{{item.address}}</div>
							</van-col>
							<van-col span="5" v-if="0">
								<div class="distance">1000米</div>
							</van-col>
						</van-row>
						<div class="address-desc ellipsis">{{item.name}}</div>
					</div>
				</div>
				<van-empty description="未匹配到地址~" v-else />
			</template>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import { mapState, mapMutations } from "vuex"
	import PublicHeader from '@/components/header'
	export default {
		data() {
			return {
				city: {
					title: '',
					url: '',
				},
				getLocationFail: false,
				addresses: [],
				searchAddress: [],
				showSearch: false,
				key: '',
				getNowLocationing: false,
				MapType: 'gaode',
				showPreLoading: true
			}
		},
		components: {
			PublicHeader,
		},
		watch: {
			key() {
				let key= this.key;
				this.showSearch = key ? true : false;
				this.searchAddress = [];
				var url = 'system/common/map/suggestion';
				if(this.MapType == 'google') {
					url = 'system/common/map/suggestion_google';
				}
				if(key) {
					this.util.request({
						url: url,
						data: {
							key: key
						}
					}).then((res) => {
						let result = res.data.message;
						if(result.errno) {
							return;
						}
						this.searchAddress = [...result.message];
					})
				}
			}
		},
		methods: {
			...mapMutations(['setLocation', 'getLocation']),

			onLoad() {
				this.util.request({
					url: 'wmall/home/newLocation/index',
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						return;
					}
					result = result.message;
					this.addresses = [...this.addresses, ...result.addresses];
					this.city = this.util.extend(this.city, result.city);
					if(!this.city.title && this.locationInfo.city) {
						this.city.title = this.locationInfo.city;
					}
					this.MapType = this.util.getStorage('MapType');
				})
			},

			chooseLocation(item, is_search = false) {
				var locationInfo = {
					last_location_x: this.locationInfo.location_x,
					address: item.address,
					location_x: item.location_x,
					location_y: item.location_y,
					city: this.city.title,
				}
				this.setLocation(locationInfo);
				if(is_search) {
					locationInfo.name = item.name;
				}

				this.$router.go(-1);
			},

			onGetNowLocation() {
				var that = this;
				that.getNowLocationing = true;
				that.util.getLocation({
					successLocation: function(res) {
					},
					successAddress:  function(res) {
						that.setLocation({
							location_x: res.location_x,
							location_y: res.location_y,
							address: res.address,
							province: res.province,
							city: res.city,
							district: res.district,
							township: res.township
						});
						that.getNowLocationing = false;
						this.$router.go(-1);
					},
					fail: function(res) {
						that.getNowLocationing = false;
						that.util.$toast('获取定位失败,你可以手动搜索收货地址');
					},
					MapType: that.MapType
				});
			},

			onInit() {
				var that = this;
				that.getLocation();
				if(!that.locationInfo.location_x) {
					that.util.getLocation({
						successLocation: function(res) {
							that.setLocation({
								location_x: res.location_x,
								location_y: res.location_y
							});
							that.onLoad();
						},
						successAddress: function(res) {
							that.setLocation({
								location_x: res.location_x,
								location_y: res.location_y,
								address: res.address,
								province: res.province,
								city: res.city,
								district: res.district,
								township: res.township
							});
						},
						fail: function(res) {
							that.setLocation({
								last_location_x: 0,
								location_x: 0,
								address: '获取定位失败'
							});
							that.getLocationFail = true;
							that.onLoad();
						}
					});
				} else {
					that.getLocationFail = false;
					that.locationInfo.last_location_x = this.locationInfo.location_x;
					that.onLoad();
				}
			}
		},
		computed: {
			...mapState([
				'locationInfo'
			]),
		},
		mounted() {
			if(!this.$route.query.m) {
				this.onInit();
			} else {
				this.onLoad();
			}
			
		},
	}
</script>

<style>
	#location .container{
		min-height: 100vh;
		background-color: #fff;
	}
	#location .search-bar{
		position: sticky;
		top: 46px;
		z-index: 1;
	}
	#location .search-bar .search-left{
		display: flex;
		align-items: center;
		font-size: 14px;
		margin-right: 10px;
	}
	#location .search-bar .search-left .icon-location{
		font-size: 16px;
		color: #333;
		margin-right: 3px;
	}
	#location .search-bar .search-left .icon-unfold{
		font-size: 12px;
		margin-left: 5px;
	}
	#location .search-bar .search-left .city{
		max-width: 55px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	#location .now-address{
		padding: 10px 12px 15px;
		background-color: #fff;
		font-size: 14px;
	}
	#location .now-address .address-text{
		width: 100%;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		font-size: 15px;
	}
	#location .now-address .to-location{
		display: flex;
		align-items: center;
		justify-content: flex-end;
		color: var(--color);
	}
	#location .headline{
		position: sticky;
		top: 100px;
		z-index: 1;
		display: flex;
		align-items: center;
		padding: 15px 12px 5px 32px;
		background-color: #fff;
		font-size: 14px;
		color: #7A7A7A;
		background-color: #fff;
	}
	#location .headline .icon{
		position: absolute;
		z-index: 1;
		left: 12px;
		top: 38%;
	}
	#location .address-list{
		padding: 0 12px 15px 32px;
		background-color: #fff;
	}
	#location .address-list .address-item{
		padding: 10px 0;
	}
	#location .address-list .address-item .label{
		display: inline-flex;
		align-items: center;
		justify-content: center;
		width: 35px;
		padding: 3px 0;
		transform: scale(.9);
		transform-origin: 0 0;
		border-radius: 3px;
		font-size: 12px;
		color: #fff;
	}
	#location .address-list .address-item .label.label-1{
		background-color: #FEF2D7;
		color: #F3AE18;
	}
	#location .address-list .address-item .label.label-2{
		background-color: #E2EDFC;
		color: #5996E9;
	}
	#location .address-list .address-item .label.label-3{
		background-color: #EDF9E6;
		color: #8FCE5F;
	}
	#location .address-list .address-item .address-text{
		width: 100%;
		line-height: 1.2;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		font-size: 14px;
		font-weight: bold;
	}
	#location .address-list .address-item .member{
		display: flex;
		align-items: center;
		justify-content: flex-start;
		margin-top: 8px;
		color: #7a7a7a;
		font-size: 12px;
	}
	#location .address-list .address-item .member .sex{
		margin: 0 20px 0 10px;
	}

	#location .search-list{
		padding-left: 15px;
		background-color: #fff;
		font-size: 14px;
	}
	#location .search-item{
		padding: 10px 15px 10px 0;
	}
	#location .search-item .address-text{
		width: 100%;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	#location .search-item .distance{
		font-size: 12px;
		color: #7a7a7a;
		text-align: right;
	}
	#location .address-desc{
		width: 100%;
		color: #7a7a7a;
		font-size: 12px;
		margin-top: 8px;
	}
</style>


